@font-face {
  font-family: "icomoon";
  src: url("data:application/x-font-ttf;charset=utf-8;base64,")
    format("truetype");
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"],
[class*=" icon-"] {
  font-family: "icomoon";
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-spinner:before {
  content: "\e000";
}

@keyframes anim-rotate {
  0% {
    transform: rotate(0);
  }
  100% {
    transform: rotate(360deg);
  }
}
.spinner {
  font-size: 4em;
  height: 1em;
  line-height: 1;
  margin: auto;
  animation: anim-rotate 2s infinite linear;
  color: black;
  text-shadow: 0 0 0.25em rgba(255, 255, 255, 0.3);
}
.spinner--steps {
  animation: anim-rotate 1s infinite steps(8);
}
.spinner--steps2 {
  animation: anim-rotate 1s infinite steps(12);
}

.talign-center {
  height:100vh;
  width: 100%;
  display: flex;
  font-family: sans-serif;
  line-height: 1.5;
}
